.footer {
  padding: 2rem 0;
  background-color: #0D1014;

  @include media('>=widescreen') {
    padding: 5rem 0 4.3rem;
  }

  &__top {
    margin-bottom: 2.4rem;

    @include media('>=widescreen') {
      margin-bottom: 4.7rem;
    }
  }

  &__bottom {
    padding-top: 2.4rem;
    border-top: 1px solid #2F363C;
    text-align: center;

    @include media('>=widescreen') {
      padding-top: 5rem;
    }
  }

  &__logo {
    position: relative;
    z-index: 6;
    width: 8.9rem;
    height: 2.6rem;
    display: flex;
    align-items: center;
    margin-right: 2rem;
  }

  &__links {
    @include media('>=tablet') {
      display: flex;
      justify-content: space-between;
    }
  }

  &__copyright {
    color: #959FA7;
    margin-bottom: 1.6rem;

    @include media('>=tablet') {
      margin: 0 2rem 0 0;
    }
  }
}

.link-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0 -0.8rem -1.6rem;

  @include media('>=tablet') {
    margin: 0 -2.5rem -1.6rem 0;
  }

  &__item {
    padding: 0 0.8rem;
    margin-bottom: 1.6rem;

    @include media('>=tablet') {
      width: fit-content;
      padding: 0;
      margin: 0 2.5rem 1.6rem 0;
    }
  }
}

.nav-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -0.8rem;

  @include media('>=tablet') {
    margin: 0 -3rem -1.6rem 0;
  }

  &__item {
    width: 50%;
    padding: 0 0.8rem;
    margin-bottom: 1.6rem;

    @include media('>=tablet') {
      width: fit-content;
      padding: 0;
      margin: 0 3rem 1.6rem 0;
    }
  }
}

.nav-link {
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 1.25;
  color: #959FA7;
  transition: $base-transition;

  &:hover {
    color: $white;
  }
}

.socials {
  display: flex;
  align-items: center;

  &__item {
    &+& {
      margin-left: 2.5rem;
    }
  }

  &__link {
    width: 2.3rem;
    height: 2.3rem;
    transition: $base-transition;
    opacity: 0.5;

    &:hover {
      opacity: 1;
    }
  }
}