.services-section {
  padding: 8rem 0 2rem;
  background-image: url('../images/services-bg-sm.webp');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;

  @include media('>=tablet') {
    background-image: url('../images/services-bg.webp');
  }

  @include media('>=desktop') {
    padding: 11rem 0 10rem;
    background-image:linear-gradient(180deg, #000000 0%, rgba(0, 0, 0, 0) 20.05%), linear-gradient(180deg, rgba(0, 0, 0, 0) 82.87%, #000000 100%), url('../images/services-bg-lg.webp');
  }

  &::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 5%;
    bottom: 0;
    z-index: 0;
    background: linear-gradient(0deg, #000000 8.46%, rgba(0, 0, 0, 0) 100%);

    @include media('>=desktop') {
      height: 20%;
    }
  }
}

.services-head {
  margin-bottom: 5.4rem;
  max-width: 66rem;

  @include media('>=desktop') {
    margin-bottom: 7.4rem;
  }
}

.services-swiper {
  position: relative;

  &__buttons {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 2rem auto 0;

    @include media('>=desktop') {
      margin: 0;
      position: absolute;
      top: -11.5rem;
      right: 0;
    }

    .swiper-btn+.swiper-btn {
      margin-left: 2rem;
    }
  }

  .swiper-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 4.6rem;
    height: 4.6rem;
    border-radius: 50%;
    background-color: #2B2B2B;
    color: $white;
    cursor: pointer;
    transition: $base-transition;

    &:hover {
      background-color: #3f3f3f;
      color: #cda6ff;
    }

    &-prev {
      padding-right: 4px;
    }

    &-next {
      padding-left: 4px;
    }

    svg {
      transition: $base-transition;
    }
  }

  .swiper-button-disabled {
    color: #8B8B8B;
    pointer-events: none;
  }
}

.services {
  max-width: none;

  @include media('>=desktop') {
    max-width: 42.4rem;
  }

  &__img {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 2.8rem;
    height: 2.8rem;
    position: relative;
    margin-left: 1.5rem;
    margin-bottom: 4rem;
  }
}