.contact-section {
  padding: 6.5rem 0 4rem;

  @include media('>=desktop') {
    padding: 0 0 19.4rem;
  }
}

.contact {

  &__wrap {
    position: relative;
    padding: 3rem 2rem;
    border-radius: 15px;
    overflow: hidden;
    z-index: 1;

    @include media('>=tablet') {
      display: flex;
      align-items: flex-end;
    }

    @include media('>=desktop') {
      padding: 6.6rem 2.9rem;
    }
  }

  &__video {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;

    video {
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }

  &__title-col {
    margin-bottom: 3rem;

    @include media('>=tablet') {
      width: 48%;
      padding-right: 2rem;
      margin-bottom: 0;
    }

    @include media('>=desktop') {
      width: calc(100% - 509px);
      padding-right: 3.2rem;
    }

    @include media('>=widescreen') {
      padding-left: 4.2rem;
    }
  }

  &__form {
    width: 100%;

    @include media('>=tablet') {
      width: 52%;
    }

    @include media('>=desktop') {
      width: 509px;
    }
  }

  &__title {
    margin-bottom: 3rem;

    @include media('>=desktop') {
      margin-bottom: 5.6rem;
    }
  }
}