.slider-right-hidden {
  z-index: 1;

  &::before,
  &::after {
    //content: "";
    width: calc(100vw - 1205px);
    position: absolute;
    top: -4rem;
    bottom: 0;
    z-index: 10;
    z-index: 2;
    background-color: $black;
    background-image: url('../images/services-bg.webp');
    display: none;

    @include media(">=widescreen") {
      display: block;
    }
  }

  &::after {
    left: calc(100% + 4rem);
    background-position: right;
  }

  &::before {
    right: calc(100% + 4rem);
    background-position: left bottom;
  }
}

.slider {
  position: relative;

  .swiper-btn {
    position: absolute;
    bottom: 0;
    z-index: 2;

    @include media('>=tablet') {
      bottom: auto;
      top: 0;
    }

    &-prev {
      left: -6.6rem;
    }

    &-next {
      right: -6.6rem;
    }

    &-center {
      right: 60%;
      transform: translateX(-60%);

      @include media(">=desktop") {
        right: 63rem;
        transform: none;
      }

      @include media(">=widescreen") {
        right: 61rem;
      }
    }
  }
}

.service-slider {
  padding: 0 0 4.6rem;

  @include media('>=tablet') {
    padding: 3.8rem 0 0;
  }

  @include media(">=widescreen") {
    padding: 4.8rem 0 0;
  }

  .card {
    height: 100%;
    box-sizing: border-box;
  }
}