.main-title {
  font-weight: 600;
  font-size: 3rem;
  line-height: 1.12;

  @include media('>=tablet') {
    font-size: 4.8rem;
  }

  @include media('>=desktop') {
    font-size: 7.6rem;
  }
}

.section-title {
  font-weight: 600;
  font-size: 3.2rem;
  line-height: 1.23;

  @include media('>=tablet') {
    font-size: 4rem;
  }

  @include media('>=desktop') {
    font-size: 6.4rem;
  }
}

.big-text {
  font-weight: 600;
  font-size: 2.1rem;
  line-height: 1.23;
}

.base-text {
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 1.22;
  color: $main;
}

.small-text {
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 1.25;
}

.link-underline {
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 1.25;
  color: $white;
  border-bottom: 1px solid transparent;
  transition: $base-transition;

  &:hover {
    border-bottom: 1px solid $white;
  }
}

//gradient

.gradient-text {
  background: linear-gradient(99.61deg, #cda6ff 13.74%, #87e097 87.84%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
}

.gradient-line {
  display: flex;
  max-width: 100%;
  height: 1px;
  background: linear-gradient(99.61deg, #cda6ff 13.74%, #87e097 87.84%);
}

//align

.text-left {
  text-align: start;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: end;
}

//color

.color-white {
  color: $white;
}

.color-black {
  color: $black;
}

.color-main {
  color: $main;
}