// grid

.row {
  display: flex;
  flex-wrap: wrap;

  &--how {
    margin: 0 -2.5rem -6.5rem;

    .col {
      padding: 0 2.5rem;
      margin-bottom: 6.5rem;
    }
  }

  &--portfolio {
    justify-content: center;
    margin: calc(-3.5rem / 2);

    .col {
      display: flex;
      justify-content: center;
      padding: calc(3.5rem / 2);
    }
  }

  &--why {
    justify-content: center;
    margin: calc(-3.5rem / 2);

    .col {
      display: flex;
      justify-content: center;
      padding: calc(3.5rem / 2);
    }
  }
}

.col {
  width: 100%;

  &.col-12 {
    width: 100%;
  }

  &.col-6 {
    width: 50%;
  }

  &.col-4 {
    width: calc(100% / 3);
  }

  &.col-3 {
    width: 25%;
  }

  &.col-2 {
    width: calc(100% / 6);
  }

  &.col-1 {
    width: calc(100% / 12);
  }

  @include media('>=tablet') {
    &.col-tb-12 {
      width: 100%;
    }

    &.col-tb-6 {
      width: 50%;
    }

    &.col-tb-4 {
      width: calc(100% / 3);
    }

    &.col-tb-3 {
      width: 25%;
    }

    &.col-tb-2 {
      width: calc(100% / 6);
    }

    &.col-tb-1 {
      width: calc(100% / 12);
    }
  }

  @include media('>=desktop') {
    &.col-lg-12 {
      width: 100%;
    }

    &.col-lg-6 {
      width: 50%;
    }

    &.col-lg-4 {
      width: calc(100% / 3);
    }

    &.col-lg-3 {
      width: 25%;
    }

    &.col-lg-2 {
      width: calc(100% / 6);
    }

    &.col-lg-1 {
      width: calc(100% / 12);
    }
  }
}