.intro-section {
  padding: 14rem 0 8rem;
  background-image:  linear-gradient(180deg, rgba(0, 0, 0, 0) 77.41%, rgba(0, 0, 0, 0.7) 93.82%), url('../images/intro-bg.webp');
  background-repeat: no-repeat;
  background-size: cover;

  @include media('>=desktop') {
    padding: 23.1rem 0 5.8rem;
    min-height: 100vh;
    background-position: center;
  }
}

.intro-wrap {
  margin-bottom: 6rem;

  @include media('>=desktop') {
    margin-bottom: 11.4rem;
  }
}

.intro-info {
  &__wrap {
    position: relative;
    display: flex;
    background-color: $bg-1;
    width: 100%;
    border: 1px solid $border;
    border-radius: 10px;
  }

  &__content {
    max-width: 70.4rem;
    padding: 2.4rem 2rem;
    margin-right: auto;

    @include media('>=desktop') {
      padding: 5.6rem 6rem 4.2rem;
    }
  }

  &__text {
    font-size: 1.8rem;
    font-weight: 400;
    color: $main;
    line-height: 1.25;
    margin-bottom: 2rem;

    @include media('>=desktop') {
      margin-bottom: 5rem;
      font-size: 2.4rem;
    }

    span {
      color: $white;
      font-weight: 500;
    }
  }

  &__link {
    max-width: max-content;
  }

  &__btn {
    position: relative;
    font-weight: 600;
    font-size: 1.6rem;
    color: $white;
    margin-bottom: 3px;

    @include media('>=desktop') {
      font-size: 1.8rem;
    }

    &::after {
      content: url('../images/arrow.svg');
      position: absolute;
      padding-left: 1rem;
      transition: $base-transition;
    }

    &:hover::after {
      padding-left: 3rem;
      opacity: 1;
    }
  }

  &__img {
    display: none;
    position: absolute;
    top: 0;
    right: 0;

    @include media('>=desktop') {
      display: block;
    }
  }
}