.header {
  position: fixed;
  z-index: 10;
  left: 0;
  top: 70px;
  right: 0;
  transform: translateY(-70px);
  width: 100%;
  padding: 1.1rem 0;
  border-bottom: 1px solid $border;

  &.fixed {
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
  }

  &__wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__btn {
    justify-content: flex-start;
    margin-right: auto;
  }

  .logo {
    margin-right: 2rem;
  }
}

.nav {
  position: fixed;
  z-index: 5;
  top: 0;
  right: -100vw;
  width: 100%;
  height: 100vh;
  padding: 8rem 2rem 10rem;
  background-color: $bg-1;
  transition: $base-transition;
  display: flex;
  flex-direction: column;

  @include media('>=desktop') {
    position: static;
    right: auto;
    background-color: transparent;
    padding: 0;
    height: auto;
    flex-direction: row;
  }

  &.active {
    right: 0;
  }

  &__list {
    margin-bottom: 4rem;

    @include media('>=desktop') {
      margin: 0 3rem 0 0;
      display: flex;
      align-items: center;
    }
  }

  &__item {
    &+& {
      margin-top: 2rem;

      @include media('>=desktop') {
        margin: 0 0 0 3rem;
      }
    }
  }

  &__link {
    font-weight: 400;
    font-size: 2rem;
    line-height: 1.25;
    color: #959FA7;
    transition: $base-transition;

    @include media('>=desktop') {
      font-size: 1.6rem;
    }

    &:hover {
      color: $white;
    }
  }

  &__btn {
    margin: auto auto 0;
  }
}

.logo {
  position: relative;
  z-index: 6;
  width: 8.9rem;
  height: 2.6rem;
  display: flex;
  align-items: center;
}

.burger {
  width: 30px;
  height: 30px;
  position: relative;
  z-index: 10;
  @extend %d-flex-column;
  justify-content: center;
  align-items: center;
  cursor: pointer;

  @include media(">=desktop") {
    display: none;
  }

  &.active {
    span {
      position: absolute;

      &:nth-child(1) {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(45deg);
        margin: 0;
      }

      &:nth-child(2) {
        opacity: 0;
      }

      &:nth-child(3) {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(-45deg);
        margin: 0;
      }
    }
  }

  span {
    width: 80%;
    height: 2px;
    border-radius: 1px;
    background-color: $white;
    transition: $base-transition;

    &:nth-child(1) {
      margin-bottom: 4px;
    }

    &:nth-child(3) {
      margin-top: 4px;
    }
  }
}