.platforms-section {
  padding: 8rem 0 4rem;

  @include media('>=desktop') {
    padding: 0rem 0 7.7rem;
  }
}

.platforms {
  max-width: 98rem;
  margin: 0 auto;
  display: flex;
}

.platforms-wrap {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: -0.75rem -0.3rem;
  position: relative;

  &::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 40%;
    bottom: -6rem;
    z-index: 0;
    background: linear-gradient(0deg, #000000 8.46%, rgba(0, 0, 0, 0) 100%);

    @include media('>=tablet') {
      height: 100%;
    }
  }
}

.platform {
  padding: 0.75rem 0.3rem;
  width: max-content;

  &__border {
    background: linear-gradient(99.61deg, #cda6ff 13.74%, #87e097 87.84%);
    padding: 1px;
    border-radius: 5.5rem;
  }

  &__wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.8rem 1.6rem;
    background-color: $black;
    border-radius: 5.5rem;
  }
}