.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 1rem;
  font-size: 1.6rem;
  font-weight: 600;
  padding: 0.4rem 2.4rem;
  transition: $base-transition;
  min-height: 4.2rem;

  &.primary {
    background-color: $white;
    color: $black;

    @include media('>=desktop') {
      &:hover,
      &:active,
      &.active {
        background-color: #cda6ff;
      }
    }
  }

  &.secondary {
    background-color: rgba(255, 255, 255, 0.15);
    color: $white;

    @include media('>=desktop') {
      &:hover,
      &:active,
      &.active {
        background-color: rgba(255, 255, 255, 0.35);
      }
    }
  }

  &.tertiary {
    
  }

  &.disabled {}

  &.big {
    @include media('>=desktop') {
      min-height: 5rem;
      font-size: 1.8rem;
    }
  }
}