.why-section {
  padding: 8rem 0 6rem;
  background-image: url('../images/why-bg.webp');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  @include media('>=desktop') {
    padding: 13.9rem 0 17rem;
  }

  @include media('>=widescreen') {
    background-image: linear-gradient(180deg, #000000 0%, rgba(0, 0, 0, 0) 20.05%), linear-gradient(180deg, rgba(0, 0, 0, 0) 82.87%, #000000 100%), url('../images/why-bg-lg.webp');
  }

  .section-title {
    margin-bottom: 5rem;

    @include media('>=desktop') {
      margin-bottom: 7rem;
    }
  }
}

.why {
  &__wrap {
    @extend %d-flex-column-start;
  }

  &__img {
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 2.8rem;
    position: relative;
    z-index: 2;
    margin-left: 1.5rem;
    margin-bottom: 4rem;
  }
}