.form {

  &__wrap {
    background-color: $black;
    border-radius: 15px;
    padding: 3rem 2rem;

    @include media('>=tablet') {
      padding: 3.2rem 2.4rem
    }

    @include media('>=widescreen') {
      padding: 3.4rem 5.6rem 6.3rem 4.1rem;
    }
  }

  &__field {
    position: relative;
    padding-top: 1.6rem;

    &.invalid {
      .form__valid {
        display: block;
      }

      .form__input {
        color: #cd0606;
        border-bottom: 1px solid #cd0606 !important;
      }

      .form__label {
        opacity: 1;
      }
    }

    &+& {
      margin-top: 1.8rem;
    }
  }

  &__input {
    width: 100%;
    padding: 0 0 0.8rem;
    position: relative;
    z-index: 2;
    font-weight: 400;
    font-size: 1.4rem;
    line-height: 1.22;
    color: $white;
    background-color: transparent;
    border-bottom: 1px solid #2F363C !important;

    @include media('>=desktop') {
      font-size: 1.8rem;
      padding: 0 0 1.2rem;
    }

    &::placeholder {
      color: #959FA7;
      font-weight: 400;
      font-size: 1.8rem;
      line-height: 1.22;
    }

    &:focus {
      &+.form__label {
        opacity: 1;
      }

      &::placeholder {
        color: transparent !important;
      }
    }

    &--textarea {
      height: 35px;
      resize: none;

      &+.form__label {
        opacity: 1;
      }

      &:placeholder-shown+.form__label {
        opacity: 0;
      }

      &:focus+.form__label {
        opacity: 1;
      }
    }
  }

  &__label {
    opacity: 0;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    font-weight: 400;
    font-size: 1.2rem;
    line-height: 1;
    color: $white;
    transition: all 0.3s ease;

    &.active {
      opacity: 1;
    }
  }

  &__valid {
    position: relative;
    z-index: 1;
    font-weight: 400;
    font-size: 1.1rem;
    line-height: 1.6;
    color: #ff0000;
    margin-top: 8px;
    display: none;
  }

  &__btn {
    margin-top: 4rem;

    @include media('>=widescreen') {
      margin-top: 9rem;
    }
  }
}