.how-section {
  padding: 8rem 0 2rem;
  background-image: linear-gradient(180deg, #000000 0%, rgba(0, 0, 0, 0) 18.72%), url('../images/how-sm-bg.webp');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 5%;
  position: relative;
  z-index: 2;

  @include media('>=tablet') {
    background-image: linear-gradient(180deg, #000000 0%, rgba(0, 0, 0, 0) 18.72%), url('../images/how-bg.webp');
  }

  @include media('>=desktop') {
    padding: 8.2rem 0 12rem;
    background-image: linear-gradient(180deg, #000000 0%, rgba(0, 0, 0, 0) 18.72%), url('../images/how-bg-lg.webp');
  }

  @include media('>=widescreen-lg') {
    background-image: linear-gradient(180deg, #000000 0%, rgba(0, 0, 0, 0) 18.72%), linear-gradient(180deg, rgba(0, 0, 0, 0) 80.14%, #000000 98.63%), url('../images/how-bg-xxl.webp');
  }
}

.how-wrap {
  padding: 9rem 0 2rem;
}

.how {
  max-width: 30rem;

  &__img {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 2.8rem;
    margin: 0 0 4.1rem 1.6rem;
  }
}