// Breakpoints
$breakpoints: (
  'phone-sm': 320px,
  'phone-md': 375px,
  'phone': 480px,
  'phone-lg': 576px,
  'tablet': 768px,
  'tablet-lg': 992px,
  'desktop': 1024px,
  'widescreen': 1200px,
  'widescreen-md': 1360px,
  'widescreen-lg': 1440px,
  'full-hd': 1920px,
);

// Colors
$black: #000000;
$white: #ffffff;

$main: #959fa7;

$border: #2f363c;
$bg-1: #0d1014;

$success: #28a745;
$warning: #ffc117;
$error: #dc3545;

// Fonts
$primary-font: 'Basier Circle';

// Other
$base-transition: all 0.3s ease;

.blur-bg {
  position: absolute;
  z-index: 0;
  background: rgba(205, 166, 255, 0.4);
  filter: blur(15px);
  width: 6rem;
  height: 6rem;
  border-radius: 50%;
}

// Indents
$indent: 10px;
