.about-section {
  padding: 5.3rem 0 0;

  @include media('>=desktop') {
    padding: 6.45rem 0 0;
  }
}

.about {

  &__wrap {
    display: flex;
    flex-wrap: wrap;
    position: relative;

    &::before,
    &::after {
      content: "";
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      height: 1px;
      width: 100vw;
      background-color: $border;
    }

    &::before {
      top: 0;
    }

    &::after {
      bottom: 0;
    }
  }

  &__details {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;

    @include media('>=desktop') {
      justify-content: flex-start;
      width: calc(100% - 59.5rem);
    }
  }

  &__category {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;

    @include media('>=desktop') {
      max-width: 23.2rem;
      border-left: 1px solid $border;
      border-right: 1px solid $border;

      &+& {
        @include media('>=desktop') {
          border-left: none;
        }
      }
    }
  }

  &__category-wrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 4rem 0 5rem;
  }

  &__icon {
    max-width: 2.8rem;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    margin-bottom: 2.5rem;

    img {
      display: block;
      width: 100%;
    }
  }

  &__number {
    font-weight: 600;
    font-size: 3rem;
    color: $white;

    @include media('>=desktop') {
      font-size: 6.4rem;
    }
  }

  &__descr {
    font-weight: 400;
    font-size: 1.8rem;
    color: $white;
    opacity: 0.8;
  }

  &__text {
    padding: 4rem 0 5rem;
    margin: 0 auto;
    text-align: center;
    width: 100%;

    @include media('>=desktop') {
      margin: 0 auto;
      text-align: left;
      width: 59.5rem;
      padding-left: 3rem;
    }

    .section-title {
      margin-bottom: 1.7rem;
    }
  }
}