.portfolio-section {
  padding: 8rem 0 4rem;

  @include media('>=desktop') {
    padding: 8.3rem 0 3.3rem;
  }
}

.portfolio {
  width: 100%;

  &__wrap {
    padding: 2rem 2rem 3rem;
    background-color: $bg-1;
    border-radius: 1.5rem;
    border: 1px solid $border;
    height: 100%;
  }

  &__img {
    width: 100%;
    max-height: 32rem;
    display: block;
    border-radius: 2rem;
    overflow: hidden;

    @include media('>=desktop') {
      max-height: 21.4rem;
    }

    img {
      display: block;
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
  }
}

.portfolio-more {
  width: 100%;

  &__wrap {
    padding: 8.5rem 0 11rem;
    background-color: $bg-1;
    border-radius: 1.5rem;
    border: 1px solid $border;
    overflow: hidden;
    position: relative;

    @include media('>=desktop') {
      padding: 8.5rem 0 11rem;
    }
  }

  &__title {
    font-weight: 600;
    font-size: 3.2rem;
    color: $white;
    text-align: center;
  }

  &__text {
    position: relative;
    z-index: 1;
    max-width: 46rem;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
  }

  &__card1,
  &__card2 {
    position: absolute;
    top: 0;
    bottom: 0;
    z-index: 0;

    img {
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
  }

  &__card1 {
    left: 0;
  }

  &__card2 {
    right: 0;
  }

  .shadow {
    position: absolute;
    height: 100%;
    bottom: -5%;
    background: linear-gradient(0deg, #000000 8.46%, rgba(0, 0, 0, 0) 100%);
    right: 0px;
    left: 0px;

    @include media('>=tablet') {
      bottom: -40%;
    }
  }
}